import React from "react";

const ContactListHeader = () => {
  return (
    <thead>
      <tr>
        <th scope="col">Avatar</th>
        <th scope="col">Nombre</th>
        <th scope="col">Teléfono</th>
        <th scope="col">Correo</th>
        <th scope="col">Borrar</th>
      </tr>
    </thead>
  );
};

export default ContactListHeader;
