import React, { Component } from "react";
import Navbar from "./components/Navbar";
import ContactList from "./components/ContactList";
import AddContact from "./components/AddContact";
import "./App.css";

class App extends Component {
  state = {
    simpsonsData: [
      {
        avatar: "https://i.imgur.com/K4V74Og.png",
        name: "Homero Simpson",
        phone: "939-555-0113",
        email: "thehomer@thesimpsons.com"
      },
      {
        avatar: "https://i.imgur.com/a11mBwo.png",
        name: "Bart Simpson",
        phone: "939-555-0113",
        email: "elbarto@thesimpsons.com"
      },
      {
        avatar: "https://i.imgur.com/NB1irRo.png",
        name: "Moe Szyslak",
        phone: "939-555-1239",
        email: "moe@moestavern.com"
      },
      {
        avatar: "https://i.imgur.com/1pWbN0Q.png",
        name: "Señor Burns",
        phone: "888-155-7915",
        email: "mrburns@plantanucleardespringfield.com"
      },
      {
        avatar: "https://i.imgur.com/EnxKz1R.png",
        name: "Ned Flanders",
        phone: "123-444-9999",
        email: "nedflanders@zurdorama.com"
      },
      {
        avatar: "https://i.imgur.com/lP95MnE.png",
        name: "Director Skinner",
        phone: "939-333-1224",
        email: "skinner@primariaspringfield.com"
      },
      {
        avatar: "https://i.imgur.com/Lbgp81H.png",
        name: "Apu Nahasapeemapetilon",
        phone: "939-333-9999",
        email: "apu@kwik-e-mart-franchises.com"
      }
    ]
  };

  borrarContacto = idx => {
    const { simpsonsData } = this.state;

    this.setState({
      simpsonsData: simpsonsData.filter((contacto, i) => {
        return i !== idx;
      })
    });
  };

  handleSubmit = contacto => {
    this.setState({ simpsonsData: [...this.state.simpsonsData, contacto] });
  };

  render() {
    const { simpsonsData } = this.state;
    return (
      <div>
        <Navbar />
        <div className="container">
          <ContactList
            simpsonsData={simpsonsData}
            borrarContacto={this.borrarContacto}
          />
        </div>
        <div className="container mt-5">
          <AddContact handleSubmit={this.handleSubmit} />
        </div>
      </div>
    );
  }
}

export default App;
