import React from "react";

const ContactListBody = props => {
  const contacto = props.simpsonsData.map((item, idx) => {
    return (
      <tr key={idx}>
        <th scope="row">
          <img src={item.avatar} />
        </th>
        <td>{item.name}</td>
        <td>{item.phone}</td>
        <td>{item.email}</td>
        <td>
          <button
            type="button"
            class="btn btn-danger"
            onClick={() => props.borrarContacto(idx)}
          >
            Delete
          </button>
        </td>
      </tr>
    );
  });

  return <tbody>{contacto}</tbody>;
};

export default ContactListBody;
